/*
*
* PortalCard
*
*/

import React from 'react';
import styled from 'styled-components';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { withRouter } from 'react-router';
import { Col, Row, ButtonGroup, Button } from 'reactstrap';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { PortalModel } from 'cb-utils/console-entity-models';
import { CustomModalBodyComponents } from 'components/BaseModal';
import NavCard from 'components/NavToDetailCard';
import { GearDropDown } from 'containers/EdgesPage';
import { CaretDownIcon, RolesIcon, SettingsIcon } from 'components/EntityIcons';
import { DownloadIcon } from 'components/Icons';
import CbDropDown from 'components/CbDropDown';
import Desc from 'components/CardDescription';
import { format } from 'date-fns';
import { getType, getVersion } from 'components/PortalCard/utils';
import { fetchPortalDetailUrl } from 'cb-utils/getUrls';

const ColWrapper = styled(Col)`
  overflow-wrap: break-word;
`;

// has to match the width of the export button
const ButtonWrap = styled(Button)`
  padding: 6px;
  border: none !important;
`;

interface PortalCardProps extends SystemRouteProps<{}> {
  portal: PortalModel;
  otherProps: {
    showModal: (component: CustomModalBodyComponents, props: object) => void;
    sysKey: string;
    sysSecret: string;
  };
}

interface PortalCardState {
  url: string;
}

export class PortalCard extends React.Component<
  PortalCardProps,
  PortalCardState
> {
  // this is the only way to retain the cards correct type and version and make sure the url is correct when
  // the card rerenders after adding or deleting new portals
  type = getType(this.props.portal.type);
  version = getVersion(this.props.portal.type, this.props.portal.name);
  state = {
    url: '',
  };

  componentWillMount() {
    fetchPortalDetailUrl(
      this.props.portal,
      this.props.otherProps.sysSecret,
    ).then((newUrl: string) => {
      this.setState({
        url: newUrl,
      });
    });
  }

  componentWillReceiveProps(nextProps: PortalCardProps) {
    fetchPortalDetailUrl(
      this.props.portal,
      this.props.otherProps.sysSecret,
    ).then((newUrl: string) => {
      this.setState({
        url: newUrl,
      });
    });
  }

  render() {
    this.type = getType(this.props.portal.type);
    this.version = getVersion(this.props.portal.type, this.props.portal.name);
    return (
      <NavCard name={this.props.portal.name} customUrl={this.state.url}>
        <Row data-cy="portal-card">
          <ColWrapper xs={6}>
            <div>{this.props.portal.name}</div>
            <div>
              <Desc>{this.props.portal.description}</Desc>
            </div>
          </ColWrapper>
          <ColWrapper xs={2}>
            <Desc>{this.type}</Desc>
          </ColWrapper>
          <ColWrapper>
            <Desc>{this.version}</Desc>
          </ColWrapper>
          <ColWrapper>
            <Desc>
              {format(
                new Date(this.props.portal.last_updated),
                `MMM DD YYYY, hh:mm a`,
              )}
            </Desc>
          </ColWrapper>
          <ButtonGroup
            size="sm"
            className="ml-auto"
            onClick={(e) => {
              e.preventDefault();
            }}
            data-cy="portal-dropdown"
          >
            <div>
              <ButtonWrap tag="div">
                <CbDropDown
                  anchorEl={<CaretDownIcon /> as any}
                  triggerEl={<GearDropDown />}
                  items={[
                    {
                      displayName: (
                        <div>
                          <SettingsIcon />{' '}
                          <FormattedMessage {...messages.settings} />
                        </div>
                      ),
                      ['data-test']: 'portal-settings',
                      onClick: () => {
                        this.props.otherProps.showModal('PortalEditModal', {
                          name: this.props.portal.name,
                          desc: this.props.portal.description,
                          url: this.state.url,
                        });
                      },
                    },
                    {
                      displayName: (
                        <div>
                          <RolesIcon />{' '}
                          <FormattedMessage {...messages.editRoles} />
                        </div>
                      ),
                      ['data-test']: 'portal-roles',
                      onClick: () => {
                        this.props.otherProps.showModal('EditRolesModal', {
                          assetClass: 'portals',
                          itemName: this.props.portal.name,
                        });
                      },
                    },
                    {
                      displayName: (
                        <div>
                          <DownloadIcon />{' '}
                          <FormattedMessage {...messages.export} />
                        </div>
                      ),
                      onClick: () => {
                        this.props.otherProps.showModal('ExportModal', {
                          assetClass: 'portals',
                          portalName: this.props.portal.name,
                        });
                      },
                    },
                  ]}
                />
              </ButtonWrap>
            </div>
          </ButtonGroup>
        </Row>
      </NavCard>
    );
  }
}

export default withRouter(PortalCard);



