/*
 *
 * PluginsTab
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { CBDispatch } from 'cb-utils/typeUtils';
import CardTable from 'components/CardTableForEntities';
import PluginCard from 'components/PluginCard';
import { pluginSelectors } from 'containers/Console/ducks';
import { createShowBaseModal } from 'containers/Console/actions/ui';
import { CustomModalBodyComponents } from 'components/BaseModal';
import { createStructuredSelector } from 'reselect';

const defaultSortKey = 'name';

const createColumnsForCardTable = () => {
  return [
    {
      component: (
        <b>
          <FormattedMessage {...messages.nameField} />
        </b>
      ),
      isSortable: true,
      sortKey: defaultSortKey,
    },
    {
      component: (
        <b>
          <FormattedMessage {...messages.urlField} />
        </b>
      ),
      sortKey: 'url',
      width: 8,
    },
  ];
};

const ContentWrapper = styled.div`
  padding-top: 1rem;
`;

export class PluginsTab extends React.Component<
  DispatchToProps & StateToProps,
  {}
> {
  render() {
    return (
      <ContentWrapper>
        <CardTable
          defaultSortKey={defaultSortKey}
          headerColumns={createColumnsForCardTable()}
          entities={this.props.plugins}
          entityCard={PluginCard}
          cardProp="plugin"
          otherProps={{
            showModal: this.props.showBaseModal,
          }}
          exportCallback={() =>
            this.props.showBaseModal('ExportModal', { assetClass: 'plugins' })
          }
        />
      </ContentWrapper>
    );
  }
}

type StateToProps = ReturnType<typeof mapStateToProps>;
const mapStateToProps = createStructuredSelector({
  plugins: pluginSelectors.wholeSlice,
});

function mapDispatchToProps(dispatch: CBDispatch) {
  return {
    showBaseModal: (component: CustomModalBodyComponents, props: object) =>
      dispatch(createShowBaseModal({ component, props })),
  };
}

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

export default connect(mapStateToProps, mapDispatchToProps)(PluginsTab);



