/**
 *
 * PluginCard
 *
 */

import React, { SFC } from 'react';
import styled from 'styled-components';
import { withRouter } from 'react-router';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { Col, Row, ButtonGroup, Button } from 'reactstrap';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { PluginModel } from 'cb-utils/console-entity-models/plugins';
import { CardContent } from 'material-ui-next/Card';
import HoverListItem from 'components/HoverListItem';
import Desc from 'components/CardDescription';
import { GearDropDown } from 'containers/EdgesPage';
import { CaretDownIcon, SettingsIcon } from 'components/EntityIcons';
import CbDropDown from 'components/CbDropDown';
import { CustomModalBodyComponents } from 'components/BaseModal';
import styleVars from 'cssOverrides/styleVars';
import { DownloadIcon } from '../Icons';
const ColWrapper = styled(Col)`
  overflow-wrap: break-word;
`;

// has to match the width of the export button
const ButtonWrap = styled(Button)`
  padding: 0px 6px 6px 6px;
  border: none !important;
`;

const Name = styled.div`
  color: ${styleVars.primary};
`;

const Anchor = styled.a`
  text-decoration: none !important;
`;

interface PluginCardProps extends SystemRouteProps<{}> {
  plugin: PluginModel;
  otherProps: {
    showModal: (component: CustomModalBodyComponents, props: object) => void;
  };
}

const PluginCard: SFC<PluginCardProps> = (props) => {
  return (
    <HoverListItem>
      <Anchor href={props.plugin.url} target="_blank">
        <CardContent>
          <Row>
            <ColWrapper>
              <Name>{props.plugin.name}</Name>
            </ColWrapper>
            <ColWrapper xs={8}>
              <Desc>{props.plugin.url}</Desc>
            </ColWrapper>
            <ButtonGroup
              size="sm"
              className="ml-auto"
              onClick={(e) => {
                e.preventDefault();
              }}
              data-cy="plugin-dropdown"
            >
              <div>
                <ButtonWrap tag="div">
                  <CbDropDown
                    anchorEl={<CaretDownIcon /> as any}
                    triggerEl={<GearDropDown />}
                    items={[
                      {
                        displayName: (
                          <div>
                            <SettingsIcon />{' '}
                            <FormattedMessage {...messages.settings} />
                          </div>
                        ),
                        onClick: () => {
                          props.otherProps.showModal('PluginEditModal', {
                            name: props.plugin.name,
                            url: props.plugin.url,
                          });
                        },
                        ['data-test']: 'plugin-settings',
                      },
                      {
                        displayName: (
                          <div>
                            <DownloadIcon />{' '}
                            <FormattedMessage {...messages.export} />
                          </div>
                        ),
                        onClick: () => {
                          props.otherProps.showModal('ExportModal', {
                            assetClass: 'plugins',
                            pluginName: props.plugin.name,
                          });
                        },
                      },
                    ]}
                  />
                </ButtonWrap>
              </div>
            </ButtonGroup>
          </Row>
        </CardContent>
      </Anchor>
    </HoverListItem>
  );
};

export default withRouter(PluginCard);



