/*
 *
 * PortalsTab
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import styled from 'styled-components';
import messages from './messages';
import { CBDispatch } from 'cb-utils/typeUtils';
import {
  portalSelectors,
  rolesSelectors,
  rolesActions,
} from '../Console/ducks';
import { getSystemKey } from 'cb-utils/getPathParams';
import CardTable from 'components/CardTableForEntities';
import PortalCard from 'components/PortalCard';
import LoadingIndicator from 'components/LoadingIndicator';
import { systemSelectors } from 'containers/Console/ducks';
import { CustomModalBodyComponents } from 'components/BaseModal';
import { createShowBaseModal } from 'containers/Console/actions/ui';
import { createStructuredSelector } from 'reselect';

const defaultSortKey = 'name';

const createColumnsForCardTable = () => {
  return [
    {
      component: (
        <b>
          <FormattedMessage {...messages.nameField} />
        </b>
      ),
      isSortable: true,
      sortKey: defaultSortKey,
      width: 6,
    },
    {
      component: (
        <b>
          <FormattedMessage {...messages.typeField} />
        </b>
      ),
      width: 2,
    },
    {
      component: (
        <b>
          <FormattedMessage {...messages.versionField} />
        </b>
      ),
    },
    {
      component: (
        <b>
          <FormattedMessage {...messages.updatedField} />
        </b>
      ),
      isSortable: true,
      sortKey: 'last_updated',
    },
  ];
};

const ContentWrapper = styled.div`
  padding-top: 1rem;
`;

export class PortalsTab extends React.PureComponent<
  DispatchToProps & StateToProps,
  {}
> {
  componentWillMount() {
    this.props.init();
  }

  render() {
    if (!this.props.system) return <LoadingIndicator />;
    return (
      <ContentWrapper>
        <CardTable
          data-cy="portal-card-table"
          defaultSortKey={defaultSortKey}
          headerColumns={createColumnsForCardTable()}
          entities={this.props.portals}
          entityCard={PortalCard}
          cardProp="portal"
          otherProps={{
            showModal: this.props.showBaseModal,
            sysKey: this.props.system.appID,
            sysSecret: this.props.system.appSecret,
          }}
          exportCallback={() =>
            this.props.showBaseModal('ExportModal', { assetClass: 'portals' })
          }
        />
      </ContentWrapper>
    );
  }
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  portals: portalSelectors.wholeSlice,
  system: systemSelectors.currentSystem(),
  roles: rolesSelectors.entities,
});

function mapDispatchToProps(dispatch: CBDispatch) {
  const key = getSystemKey();
  return {
    init: () => {
      dispatch(rolesActions.fetchAll.request(key));
    },
    showBaseModal: (component: CustomModalBodyComponents, props: object) =>
      dispatch(createShowBaseModal({ component, props })),
  };
}

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

export default connect(mapStateToProps, mapDispatchToProps)(PortalsTab);



