/*
 *
 * PortalsPage
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import Helmet from 'react-helmet';
import { FormattedMessage } from 'react-intl';
import { compose } from 'redux';
import messages from './messages';
import { CBDispatch } from 'cb-utils/typeUtils';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import ConsolePage from 'components/ConsolePage';
import PageHeader from 'components/ConsolePageHeader';
import consoleMessages from 'containers/Console/messages';
import Tabs from 'components/Tabs';
import PortalsTab from 'containers/PortalsTab';
import PluginsTab from 'containers/PluginsTab';
import { Button } from 'reactstrap';
import { AddIcon, PortalIcon } from 'components/EntityIcons';
import { createShowBaseModal } from 'containers/Console/actions/ui';
import {
  portalSelectors,
  pluginSelectors,
  portalActions,
  pluginActions,
} from '../Console/ducks';
import LearnMoreLink from 'components/LearnMoreLink';
import { createStructuredSelector } from 'reselect';

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
`;

interface PortalsPageProps extends SystemRouteProps<{}> {}

class PortalsPage extends React.PureComponent<
  PortalsPageProps & DispatchToProps & StateToProps,
  {}
> {
  componentWillMount() {
    this.props.init();
  }
  render() {
    return (
      <React.Fragment>
        <Helmet
          title="ClearBlade - Portals"
          meta={[
            { name: 'description', content: 'Description of PortalsPage' },
          ]}
        />
        <ConsolePage
          subheader={this.renderSubHeader()}
          isFetchingAssets={
            this.props.isFetchingPortals || this.props.isFetchingPlugins
          }
          zeroState={{
            show:
              this.props.totalPortals === 0 && this.props.totalPlugins === 0,
            title: consoleMessages.assetClassPortals,
            info: messages.zeroStateInfo,
            moreInfo: (
              <LearnMoreLink url="https://docs.clearblade.com/v/3/5-portal/v2/getting_started/" />
            ),
            icon: <PortalIcon />,
            addButton: this.renderAddButton({ size: 'lg' }),
          }}
        >
          <Wrapper>
            <Tabs tabs={this.renderTabs()} useRoutes />
          </Wrapper>
        </ConsolePage>
      </React.Fragment>
    );
  }

  renderAddButton = ({ size = 'md' }) => (
    <Button
      size={size}
      color="primary"
      data-cy="add-portal"
      onClick={this.props.showNewModal}
    >
      <AddIcon />
      &nbsp;
      <FormattedMessage {...messages.new} />
    </Button>
  );

  renderSubHeader = () => {
    return (
      <React.Fragment>
        <PageHeader
          header={
            <div>
              <PortalIcon />{' '}
              <FormattedMessage {...consoleMessages.assetClassPortals} />
            </div>
          }
          actionButton={this.renderAddButton}
        />
      </React.Fragment>
    );
  };

  renderTabs = () => {
    const tabs = [
      {
        id: 'Portals',
        title: 'Portals',
        component: <PortalsTab />,
        ['data-cy']: 'portals-tab',
      },
      {
        id: 'Plugins',
        title: 'Plugins',
        component: <PluginsTab />,
        ['data-cy']: 'plugins-tab',
      },
    ];
    return tabs;
  };
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  isFetchingPortals: portalSelectors.fetchAll.isLoading,
  isFetchingPlugins: pluginSelectors.fetchAll.isLoading,
  totalPortals: portalSelectors.entitiesCount,
  totalPlugins: pluginSelectors.entitiesCount,
});

function mapDispatchToProps(dispatch: CBDispatch, ownProps: PortalsPageProps) {
  const key = ownProps.match.params.systemKey;
  return {
    init: () => {
      dispatch(portalActions.fetchAll.request(key));
      dispatch(pluginActions.fetchAll.request(key));
    },
    showNewModal: () =>
      dispatch(
        createShowBaseModal({ component: 'NewPortalOrPlugin', props: {} }),
      ),
  };
}

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

const withConnect = connect(mapStateToProps, mapDispatchToProps);

export default compose(withConnect)(PortalsPage);



